import React, { Component } from 'react';
import {Grid, Row, Col,Form, FormControl,Table} from 'react-bootstrap'
import './App.css';
import Desayuno from './ComponentDesayuno/desayuno'
import Menu from './componentMenu/menu'
import {Button} from 'react-bootstrap';
// import db from './config/config'


class App extends Component {
constructor(props){
  super(props);
    this.state = {
      name: '',
      status: true,
      total: 0.00,
      lista:[],
      listDesayuno: []
    }
    this.countTotal=this.countTotal.bind(this);
    this.countTot=this.countTot.bind(this);
    this.deleteDesayuno=this.deleteDesayuno.bind(this);
    this.delete=this.delete.bind(this)
}

countTotal(precio,item){
  const {lista} = this.state;
  lista.push({
    item: item,
    precio: precio
  })
  this.setState({lista:lista,total: this.state.total+parseInt(precio)})
}

countTot(precio,item){
  const {listDesayuno} = this.state;
  listDesayuno.push({
    item: item,
    precio: precio
  })
  this.setState({listDesayuno:listDesayuno,total: this.state.total+parseInt(precio)})
}



deleteDesayuno(id,precio){
  const {listDesayuno}=this.state;
      if(id == id){
        listDesayuno.splice(id, 1);
     }
  this.setState({listDesayuno:listDesayuno, total:this.state.total - parseInt(precio)})
  }


delete(id,precio){
  const {lista}=this.state;
      if(id == id){
        lista.splice(id, 1);
     }
  this.setState({lista:lista, total:this.state.total - parseInt(precio)})
}

  render(){
    
    if(this.state.status){
       return (   
         <div className="bodyInit">  
         <Grid>
          <Row className="show-grid one">
           <Col xs={12} md={12} lg={12}>
           <h1>Burger Queen</h1>
           <Form onSubmit={this.handleSubmit.bind(this)}>
           <FormControl
            type="text"
            onChange={this.handleName.bind(this)}
            placeholder="Enter text" 
            />
             <Button type="submit" bsStyle="success">Ingresar</Button>
            </Form>
            </Col>
          </Row>
         </Grid>
         </div> 
    )}else{   
      return(     
      <div className="inicio">
         <Grid>
          <Row className="show-grid">
           <Col xs={12} md={12} lg={12}>
           <h1>Burger Queen</h1>
           </Col>       
         
            </Row>
            <Row>
            </Row>
            <Row className="show-grid">
            <Col xs={12} md={6} lg={6}>     
            <Desayuno
            countTot={this.countTot}
            deleteDesayuno={this.deleteDesayuno}
            />
            <br/>
            <Menu
             countTotal={this.countTotal}
             deleteMenu={this.deleteMenu}
             />
            </Col>
            <Col className="list" xs={12} md={6} lg={6}>
            <Table responsive>
  <thead>
    <tr>
    <p>Cliente: {this.state.name}</p>
    </tr>
    <tr>
      <th>Descripción</th>
      <th>Precio</th>
    </tr>
  </thead>
          {this.state.lista.map((data,key)=>{
            return(
            <tbody>
              <td>{data.item}</td>
              <td>s/{data.precio}</td>
              <td><i className="fas fa-trash" onClick={()=>this.delete(key,data.precio)}></i></td>
              </tbody>
            )
            })}
             {this.state.listDesayuno.map((data,key)=>{
            return(
              <tbody> 
                <td>{data.item}</td> 
                <td>s/{data.precio}</td>    
                <td><i className="fas fa-trash" onClick={()=>this.deleteDesayuno(key,data.precio)}></i></td>
                </tbody>
            )
            })}
          <tbody>
          <td><p>Total</p></td>
          <td>s/{this.state.total}</td>
          </tbody>        
            </Table> 
            <Button bsStyle="success" onClick={this.handleOk.bind(this)}>Enviar Pedido</Button>
            </Col>
          </Row>
         </Grid>  
         <Col xs={12} md={6} lg={6}>
           <Button bsStyle="warning" onClick={this.handleCancel.bind(this)}>Cancelar Pedido</Button>
           </Col>     
      </div>
      )      
    }
  }

handleOk(){
  alert('se envió el pedido');
  this.setState({status:true})
}

handleSubmit(event){
  event.preventDefault(event)
  if(this.state.name.trim() != ''){
    if(this.state.status){
      this.setState({status:false})
    }}else{
      alert('Debe ingresar un usuario')
    }
}

handleName(event){
  this.setState({name: event.target.value})
}

handleCancel(){
  this.setState({status:true})
}

// componentDidMount(){
//   db.collection('pedido').get().then((snapShots)=>{
//     console.log(snapShots);
//   })
// }

}
export default App;

